import React, { useState } from 'react'

const Note = ({ deleteMethod, toggleMethod, text, completed }) => {
  let inputRef
  const [editable, setEditable] = useState(false)
  const [value, setValue] = useState(text)
  const classString = completed ? 'note completed' : 'note'
  const handleEdit = () => {
    setEditable(!editable)
    inputRef.focus()
  }
  return (
    <div className={classString}>
      <input
        ref={input => (inputRef = input)}
        className={editable ? 'editable' : ''}
        type="text"
        value={value}
        disabled={!editable}
      />
      <button className='edit' onClick={handleEdit}>{editable ? 'Save' : 'Edit'}</button>
  <button className='done' onClick={() => toggleMethod()}>{completed ? 'Undone' : 'Done'}</button>
      <button className='delete' onClick={() => deleteMethod()}>Delete</button>
    </div>
  )
}

export default Note
